/* Base Theme Styles - Shared animations and utilities */

/* Shared keyframe animations */
@keyframes pulse-new {
  0%,
  100% {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 0 8px rgba(0, 200, 81, 0.6);
  }
}

@keyframes pulse-hot {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* Game page content - shared across themes */
.game-page-content {
  padding: 16px !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
