/* Page Navigation Base Styles */
/* Theme styling is in themes/*.css */

/* Mobile responsiveness base */
@media (max-width: 640px) {
  .page-navigation {
    padding: 4px 2px;
    overflow-x: auto;
  }

  .nav-container {
    min-width: fit-content;
    gap: 2px;
  }

  .nav-item {
    font-size: 10px;
    padding: 4px 6px;
  }

  .nav-icon {
    font-size: 12px;
  }

  .nav-label {
    display: inline;
  }

  /* Hide label on active item - show icon only */
  .nav-item.active .nav-label {
    display: none;
  }

  .nav-item.active {
    padding: 10px 6px;
    gap: 0;
    width: auto;
    min-width: unset;
  }
}
