/* Retro Browser Window Styles */
.browser-window,
.night .browser-window {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100% - 20px);
  max-height: calc(100% - 20px);
  background: #c0c0c0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow: 1px 1px 0 0 #000000;
  font-family: 'MS Sans Serif', 'Segoe UI', Tahoma, sans-serif;
  font-size: 11px;
  position: relative;
  z-index: 10;
}

/* Ensure browser window always looks the same regardless of parent theme */
.browser-window,
.browser-window * {
  /* Prevent dark mode inheritance */
  color-scheme: light;
}

.under-construction-footer {
  text-align: center;
  color: orangered;
}

/* Title Bar */
.browser-titlebar,
.night .browser-titlebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 22px;
  background: linear-gradient(to right, #000080, #1084d0);
  color: white;
  padding: 2px 2px 2px 4px;
  font-weight: bold;
  font-size: 11px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.browser-titlebar-text {
  display: flex;
  align-items: center;
  gap: 4px;
}

.browser-icon {
  font-size: 14px;
}

.browser-titlebar-controls {
  display: flex;
  gap: 2px;
}

.titlebar-button,
.night .titlebar-button {
  width: 16px;
  height: 14px;
  background: #c0c0c0;
  border: 1px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  font-family: 'Marlett', monospace;
  font-size: 10px;
  line-height: 10px;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
}

.titlebar-button:active {
  border-color: #808080 #ffffff #ffffff #808080;
}

.titlebar-button.close {
  font-weight: bold;
}

.titlebar-button.close:hover {
  background: #e81123;
  color: white;
}

/* Menu Bar */
.browser-menubar,
.night .browser-menubar {
  display: flex;
  gap: 12px;
  background: #f0f0f0;
  border-bottom: 1px solid #d4d0c8;
  padding: 4px 8px;
  font-size: 11px;
  color: #000;
}

.menu-item {
  cursor: pointer;
  padding: 2px 4px;
}

.menu-item:hover {
  background: #000080;
  color: white;
}

/* Theme Menu Dropdown */
.menu-item-wrapper {
  position: relative;
  display: inline-flex;
  align-items: center;
}

.theme-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #f0f0f0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  min-width: 120px;
}

.theme-dropdown button {
  display: block;
  width: 100%;
  padding: 4px 12px;
  background: none;
  border: none;
  text-align: left;
  font-family: inherit;
  font-size: 11px;
  cursor: pointer;
  color: #000;
  white-space: nowrap;
}

.theme-dropdown button:hover {
  background: #000080;
  color: white;
}

.theme-dropdown button.active {
  font-weight: bold;
}

.theme-dropdown button.active::before {
  content: '✓ ';
}

/* Navigation Bar */
.browser-navbar,
.night .browser-navbar {
  display: flex;
  gap: 4px;
  align-items: center;
  background: #f0f0f0;
  padding: 4px;
  border-bottom: 1px solid #808080;
}

.nav-buttons {
  display: flex;
  gap: 2px;
}

.nav-button,
.night .nav-button {
  width: 24px;
  height: 22px;
  background: #f0f0f0;
  border: 1px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  line-height: 1;
  color: #333;
}

.nav-button svg {
  display: block;
}

.nav-button:hover:not(:disabled),
.night .nav-button:hover:not(:disabled) {
  border-color: #808080 #ffffff #ffffff #808080;
}

.nav-button:disabled,
.night .nav-button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  color: #999;
}

.nav-addressbar {
  flex: 1;
  display: flex;
}

.addressbar-input,
.night .addressbar-input {
  width: 100%;
  padding: 2px 4px;
  background: white;
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
  font-family: 'Courier New', monospace;
  font-size: 11px;
  color: #000;
}

.nav-button.go {
  width: auto;
  padding: 0 8px;
  font-size: 11px;
}

/* Content Area */
.browser-content {
  flex: 1;
  background: white;
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  min-height: 0;
}

.browser-viewport {
  width: 100%;
  height: 100%;
  min-height: 100%;
  position: relative;
}

/* Status Bar */
.browser-statusbar,
.night .browser-statusbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 20px;
  background: #f0f0f0;
  border-top: 1px solid #ffffff;
  padding: 0 4px;
  font-size: 11px;
  color: #000;
}

.status-icon {
  padding-left: 7px;
}

.status-text {
  flex: 1;
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
  padding: 0px 4px;
  background: #f0f0f0;
}

.status-icons {
  display: flex;
  gap: 8px;
  align-items: center;
}

.status-zone {
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
  padding: 0px 4px;
}

/* Status Bar Theme Selector */
.status-theme-selector {
  position: relative;
  display: inline-block;
}

.status-theme-button {
  background: #f0f0f0;
  border: 1px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 0px 6px;
  font-family: inherit;
  font-size: 11px;
  cursor: pointer;
  color: #000;
  display: flex;
  align-items: center;
  gap: 4px;
}

.status-theme-button:hover {
  background: #e0e0e0;
}

.status-theme-button:active {
  border-color: #808080 #ffffff #ffffff #808080;
}

.night .status-theme-button {
  color: #000;
}

.status-theme-dropdown {
  position: absolute;
  bottom: 100%;
  right: 0;
  background: #f0f0f0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  min-width: 120px;
  margin-bottom: 2px;
}

.status-theme-dropdown button {
  display: block;
  width: 100%;
  padding: 4px 12px;
  background: none;
  border: none;
  text-align: left;
  font-family: inherit;
  font-size: 11px;
  cursor: pointer;
  color: #000;
  white-space: nowrap;
}

.status-theme-dropdown button:hover {
  background: #000080;
  color: white;
}

.status-theme-dropdown button.active {
  font-weight: bold;
}

.status-theme-dropdown button.active::before {
  content: '✓ ';
}

/* Minimized State (Desktop only) */
.browser-window.minimized {
  display: none;
}

/* Maximized State - Desktop: below header */
.browser-window.maximized {
  position: fixed !important;
  top: 80px !important;
  left: 0 !important;
  width: 100vw !important;
  height: calc(100vh - 80px) !important;
  max-height: calc(100vh - 80px) !important;
  z-index: 99999 !important;
  box-shadow: none;
  border: none;
}

/* Retro Scrollbar */
.browser-content::-webkit-scrollbar {
  width: 16px;
  height: 16px;
}

.browser-content::-webkit-scrollbar-track {
  background: #c0c0c0;
  border: 1px solid;
  border-color: #808080 #ffffff #ffffff #808080;
}

.browser-content::-webkit-scrollbar-thumb {
  background: #c0c0c0;
  border: 1px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
}

.browser-content::-webkit-scrollbar-button {
  background: #c0c0c0;
  border: 1px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  width: 16px;
  height: 16px;
}

/* Loading State */
@keyframes loading-bar {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

.browser-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #0000ff, #00ff00);
  animation: loading-bar 1s ease-in-out;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .browser-menubar {
    display: none;
  }

  .nav-button span {
    display: none;
  }

  .browser-titlebar {
    font-size: 10px;
  }
}

@media screen and (max-width: 540px) {
  .browser-window.maximized {
    top: 50px !important;
    height: calc(100vh - 50px) !important;
    max-height: calc(100vh - 50px) !important;
  }
  .browser-navbar {
    padding: 2px;
  }

  .nav-button {
    width: 20px;
    height: 20px;
    font-size: 12px;
  }
}

/* BSOD Easter Egg - contained within browser window */
.bsod-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #0078d7;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  animation: bsod-fadein 0.3s ease-out;
}

@keyframes bsod-fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.bsod-content {
  color: white;
  font-family: 'Segoe UI', 'MS Sans Serif', sans-serif;
  text-align: left;
  max-width: 800px;
  padding: 40px;
}

.bsod-content h1 {
  font-size: 120px;
  font-weight: 200;
  margin: 0 0 20px 0;
  line-height: 1;
}

.bsod-content .bsod-main-text {
  font-size: 24px;
  margin-bottom: 20px;
}

.bsod-content p {
  font-size: 18px;
  margin: 10px 0;
  line-height: 1.5;
}

.bsod-content .bsod-error {
  font-weight: bold;
  margin-top: 30px;
}

.bsod-content .bsod-code {
  font-family: 'Courier New', monospace;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
}

.bsod-content .bsod-hint {
  margin-top: 40px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  animation: bsod-blink 1.5s ease-in-out infinite;
}

@keyframes bsod-blink {
  0%,
  100% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
}

@media screen and (max-width: 640px) {
  .bsod-content h1 {
    font-size: 80px;
  }

  .bsod-content .bsod-main-text {
    font-size: 18px;
  }

  .bsod-content p {
    font-size: 14px;
  }

  .bsod-content {
    padding: 20px;
    max-width: 100%;
  }
}
